//   

import { useEffect, useState } from "react";
import { Row, Col } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "../css/list.css";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import Diaryitem from "../item/diaryitem";
import Button from "../components/Button";
import Header from "../components/Header";

import { changeOrder, changeEmoOrder, changeIdOrder } from "../Slice/itemSlice";

function DiaryList() {
  useEffect(() => {
    //   
    let title = document.getElementsByTagName("title")[0];
    title.innerHTML = `  - `;
  }, []);

  //  
  let List = useSelector((state) => state.item.item);
  let dispatch = useDispatch();

  // (List   )  
  let [data, setData] = useState([]);

  //   
  //   
  const offset = 1000 * 60 * 60 * 9;
  const time = new Date(new Date().getTime() + offset);
  let [date, setDate] = useState(time);

  //  
  let navigate = useNavigate();

  useEffect(() => {
    //  1  
    if (List.length >= 1) {
      //   
      const firstDay = new Date(
        date.getFullYear(),
        date.getMonth(),
        1
      ).getTime();

      const lastDay = new Date(
        date.getFullYear(),
        date.getMonth() + 1
      ).getTime();

      //        
      setData(List.filter((e) => firstDay <= e.date && e.date < lastDay));
    }
  }, [List, date]);

  return (
    <>
      {/* header */}
      <Header
        //  
        text={
          <input
            className="day"
            //    
            value={date.toISOString().slice(0, 7)}
            onChange={(e) => {
              //     
              setDate(new Date(e.target.value));
            }}
            type="month"
          />
        }
        //
        leftChild={
          <Button
            onClick={() => {
              setDate(
                new Date(
                  date.getFullYear(),
                  date.getMonth() - 1,
                  date.getDate() //
                )
              );
            }}
            text={"<"}
          />
        }
        // 
        rightChild={
          <Button
            onClick={() => {
              setDate(
                new Date(
                  date.getFullYear(),
                  date.getMonth() + 1,
                  date.getDate() //
                )
              );
            }}
            text={">"}
          />
        }
      />

      {/*   */}
      <div className="list-wrap">
        {/*     */}
        <div className="top">
          <Row>
            <Col className="order" xs={6} xl={3}>
              {/*    */}
              <select
                onChange={(e) => {
                  dispatch(changeOrder(e.target.value));
                }}
              >
                <option value={"new"}></option>
                <option value={"last"}> </option>
              </select>
              {/*  */}
            </Col>
            <Col className="order" xs={6} xl={3}>
              {/*     */}
              <select
                onChange={(e) => {
                  dispatch(changeEmoOrder(e.target.value));
                }}
              >
                <option value={"basics"}>( )</option>
                <option value={"good"}> </option>
                <option value={"bad"}> </option>
              </select>
              {/*  */}
            </Col>

            {/*  */}
            <Col className="btn-wrap" xl={6}>
              {/*   */}
              <Button
                onClick={() => {
                  navigate("/newdiary");
                  dispatch(changeIdOrder());
                }}
                text={" "}
                type={"myBtn_positive"}
              />
            </Col>
            {/*  */}
          </Row>
        </div>
        {/*     */}
        {data.map((a, i) => {
          return <Diaryitem data={data} i={i} key={i} />;
        })}

        {/*  */}
      </div>
    </>
  );
}

export default DiaryList;
