/*eslint-disable*/
// emotion 

import { useSelector } from "react-redux";

function EmotionItem(props) {
  let i = props.i;
  let isSelect = props.isSelect;
  // 
  let emotion = useSelector((state) => state.emotion);

  return (
    <div>
      <div
        //  onclick  id 
        // id   
        onClick={() => props.onClick(emotion[i].id)}
        //   (   on)
        className={`emotionItem ${isSelect ? `emotion_on` : `emotion_off`}`}
      >
        <img src={emotion[i].img} />
        <p>{emotion[i].text}</p>
      </div>
    </div>
  );
}

export default EmotionItem;
