/*     */

import { useNavigate } from "react-router-dom";
import Button from "../components/Button";
import "../css/item.css";
import { useSelector } from "react-redux";

function Diaryitem(props) {
  let i = props.i;
  let data = props.data;

  //  
  let navigate = useNavigate();

  //  
  let emotionList = useSelector((state) => state.emotion);
  // id  
  let emotion = emotionList.find((e) => e.id === data[i].emotion);
  return (
    <div className="list-box">
      <div className="info-box">
        {/* () */}
        <div
          className="img-bg"
          onClick={() => {
            navigate(`/detail/${data[i].id}`);
          }}
        >
          <img src={emotion.img} alt="img" />
        </div>
        {/*   */}
        <div
          className="content-box"
          onClick={() => {
            navigate(`/detail/${data[i].id}`);
          }}
        >
          <p className="day">
            {/*   */}
            {new Date(data[i].date).toLocaleDateString()}
          </p>
          {/* 0~10   */}
          <p>{data[i].content.slice(0, 10)}</p>
        </div>
        {/*  */}
        <div className="btn-wrap">
          <Button
            text={""}
            type={"myBtn"}
            onClick={() => {
              navigate(`/edit/${data[i].id}`);
              // List.item[i].id
            }}
          />
        </div>
      </div>
    </div>
  );
}

export default Diaryitem;
